<template>
  <div class="Dynamic_code">
    <div class="code_box">
      <h3>用于登录RoboGo高级参数修改功能</h3>
      <div class="field-box">
        <span class="cell__title">随机ID</span>

        <input
          type="text"
          class="cell__value"
          placeholder="请输入随机ID"
          v-model="randomCode"
        />
      </div>
      <div class="field-box">
        <div class="cell__title">动态密码</div>

        <input
          type="text"
          class="cell__value"
          placeholder="请输入动态密码"
          v-model="dynamicCode"
        />

        <button class="btn">
          <div class="btn-content">
            <span class="btn-text">点击获取</span>
          </div>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
import { getCode } from "@/api/user";
export default {
  setup() {
    const state = reactive({
      randomCode: "",
      dynamicCode: "",
    });
    const getCode = () => {
      state.randomCode &&
        getCode(state.randomCode).then((res) => {
          // 获取动态码
          state.dynamicCode = res.data.dynamicCode;
        });
    };
    return {
      ...toRefs(state),
      getCode,
    };
  },
};
</script>
<style scoped lang="scss">
.Dynamic_code {
  padding:.5rem .4rem;
  box-sizing: border-box;
  .code_box {
    h3 {
      font-size: 0.2rem;
      padding-bottom: .6rem;
      text-align: center;
    }
    .field-box {
      display: flex;
      align-items: center;
      margin-bottom: 0.3rem;
      .cell__title {
        font-size: 0.2rem;
        width: 18%;
        display: inline-block;
      }
      .cell__value {
        width: 55%;
        margin-left: 0.2rem;
      }
      .btn {
        font-size: 0.2rem;
        background: rgb(114, 50, 221);
        border-color: rgb(114, 50, 221);
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        height: .6rem;
        margin: 0;
        line-height: 1.2;
        padding: 0;
        width: 21%;
        margin-left:0.1rem;
        text-align: center;
        border-radius: 2px;
        cursor: pointer;
        -webkit-transition: opacity 0.2s;
        transition: opacity 0.2s;
        -webkit-appearance: none;
      }
      .btn::before {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        background-color: #000;
        border: inherit;
        border-color: #000;
        border-radius: inherit;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        opacity: 0;
        content: " ";
      }
      .btn-content {
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
        align-items: center;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        height: 100%;

        .btn-text {
          color: white;
        }
      }
      .btn-content::before {
        content: " ";
      }
    }
  }
}
input {
  outline-style: none;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 6px;
  width: 100%;
  font-size: 0.2rem;
  font-family: "Microsoft soft";
  display: inline-block;
  min-height: 0.6rem;
  box-sizing: border-box;
}
input:focus {
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(102, 175, 233, 0.6);
}
</style>
